import React from "react";
import joinClasses from "join-classes";
import "./select.css";

class Select extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showOptionList: false,
      showPlaceHolder: true,
    };
  }

  componentDidMount() {
    document.addEventListener("mousedown", this.handleClickOutside);
    this.setState({
      defaultSelectText:
        this.props.value === ""
          ? "Please select a screen period"
          : this.props.value,
    });
  }

  componentWillUnmount() {
    document.removeEventListener("mousedown", this.handleClickOutside);
  }

  handleClickOutside = (e) => {
    if (
      !e.target.classList.contains("select-option") &&
      !e.target.classList.contains("selected-text")
    ) {
      this.setState({
        showOptionList: false,
      });
    }
  };

  handleListDisplay = () => {
    this.setState((prevState) => {
      return {
        showOptionList: !prevState.showOptionList,
      };
    });
  };

  handleOptionClick = (event, option) => {
    // const option = event.target.getAttribute("data-name");
    this.props.onChange({[this.props.field]: option});
    this.setState({
      showOptionList: false,
    });
  };

  render() {
    const {error, value} = this.props;
    const {showOptionList, showPlaceHolder, defaultSelectText} = this.state;
    const styles = {};
    if (error) {
      styles.borderColor = "red";
    }
    return (
      <div className="select-container">
        <div
          className={joinClasses(
            showOptionList ? "selected-text active" : "selected-text",
            showPlaceHolder ? "placeholder-style" : ""
          )}
          style={styles}
          onClick={this.handleListDisplay}
        >
          {value && value.text ? value.text : defaultSelectText}
        </div>
        {showOptionList && (
          <ul className="select-options">
            {this.props.options.map((option) => {
              return (
                option.text !== value.text && (
                  <li
                    className="select-option"
                    data-name={option.text}
                    key={option.key}
                    onClick={(e) => this.handleOptionClick(e, option)}
                  >
                    {option.text}
                  </li>
                )
              );
            })}
          </ul>
        )}
      </div>
    );
  }
}

export default Select;
