import { Validator } from "jsonschema";
const validator = new Validator();

const validate = (data) => {
  let errors = { isInvalid: true };
  for (const property in data) {
    switch (property) {
      case "parentName":
        errors = {
          ...errors,
          ...validateName(property, data[property], "Parent Name"),
        };
        break;
      case "email":
        errors = {
          ...errors,
          ...validateEmail(property, data[property], "Email"),
        };
        break;
      case "childName":
        errors = {
          ...errors,
          ...validateName(property, data[property], "Child Name"),
        };
        break;
      case "phoneNumber":
        errors = {
          ...errors,
          ...validatePhone(property, data[property], "Phone Number"),
        };
        break;
      case "screenPeriodValue":
        errors = {
          ...errors,
          ...validateScreenPeriod(property, data[property], "Screen Period"),
        };
        break;
      default:
        console.log("invalid parameter");
        break;
    }
  }
  return errors;
};

const validateName = (property, value, displayName) => {
  const schema = { type: "string", minLength: 3, maxLength: 32 };
  const result = validator.validate(value, schema);
  return result.errors && result.errors.length
    ? { [property]: `Please enter a valid ${displayName}`, isInvalid: true }
    : { [property]: ``, isInvalid: false };
};

const validateScreenPeriod = (property, value, displayName) => {
  return value && value.text
    ? { [property]: ``, isInvalid: false } : { [property]: `Please enter a valid ${displayName}`, isInvalid: true };
};

const validatePhone = (property, value, displayName) => {
  const schema = { type: "string", minLength: 10, maxLength: 10 };
  const result = validator.validate(value, schema);
  return result.errors && result.errors.length
    ? { [property]: `Please enter a valid ${displayName}`, isInvalid: true }
    : { [property]: ``, isInvalid: false };
};

const validateEmail = (property, value, displayName) => {
  const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return !re.test(String(value).toLowerCase())
    ? { [property]: `Please enter a valid ${displayName}`, isInvalid: true }
    : { [property]: ``, isInvalid: false };
};

export default validate;
export { validate };
