import config from "../config/config";

const getKeyData = (key) => {
    // Breadth-first search.
    return findKey(config, key);
};


const findKey = (data, key) => {
    let currentNode;
    let queue = data;

    for (let index = 0; index < queue.length; index++) {
        currentNode = queue[index];
        if (currentNode.key === parseInt(key)) {
            return currentNode
        };
    }
    return null;
}
export default getKeyData;
export {
    getKeyData,
    findKey
};