import React, {useEffect, useState} from "react";
import "./survey.css";
import Button from "../../components/button/button";
import {getKeyData, findKey} from "../../util/util";
import {useHistory} from "react-router-dom";
import {filter, find, isEmpty, pick} from "rambda";
import {storeDetails, getDetails} from "../../util/storage";
import RadioGroup from "../../components/radio-group/radio";
import Label from "../../components/label/label";
import Message from "../../components/message/message";

const validationMsg = "This field is required";

const getCurrentComponent = (componentId) => {
  let currentComponent;
  const componentData = getDetails("componentData");
  if (componentData) {
    currentComponent = findKey(componentData, componentId);
  }
  return currentComponent;
};

const getSavedRadioOptionValue = (componentId, data) => {
  let savedOption = {};
  if (data.components) {
    savedOption = findKey(data.components, componentId);
    return savedOption ? savedOption.value : {};
  }
  return savedOption;
};

const Survey = (props) => {
  const componentId = props.match.params.componentId;
  const childComponentId = props.match.params.childComponentId;
  const history = useHistory();
  const savedComponentData = getCurrentComponent(childComponentId);
  const config = getKeyData(componentId);
  const component = findKey(config.components, childComponentId);
  const [showValidationMsg, setShowValidationMsg] = useState(0);
  const [userData, setUserData] = useState({});

  const defaultState = {
    key: component ? component.key : "",
    text: "",
    value: {},
    components: [],
  };

  useEffect(() => {
    setShowValidationMsg(0);
    setUserData(savedComponentData ? savedComponentData : defaultState);
  }, [childComponentId]);

  if (!config || !component) {
    return history.push(`/user-details}`);
  }

  const handleNextButtonClick = () => {
    if (
      userData.value.value === "no" ||
      (userData.value &&
        userData.value.value &&
        userData.components.length === component.components.length)
    ) {
      storeValues(component.key);
      history.push(`${component.nextUrl}`);
    } else {
      enableValidation();
    }
  };

  const handlePrevButtonClick = () => {
    history.push(`${component.prevUrl}`);
  };

  const enableValidation = () => {
    const nextValue = showValidationMsg + 1;
    setShowValidationMsg(nextValue);
  };

  const storeValues = (componentKey) => {
    let componentData = filter(
      (component) => component.key !== componentKey,
      getDetails("componentData") || []
    );
    componentData.push(userData);
    storeDetails("componentData", componentData);
  };

  const handleParentComponent = (data, component) => {
    userData.components = data.value === "yes" ? userData.components : [];
    setUserData({
      ...userData,
      ...pick(["key", "text"], component),
      value: data,
    });
  };

  const checkForUserInputYes = () => {
    return userData.value && userData.value.value === "yes";
  };

  const handleChildComponent = (userInput, childData) => {
    userData.components = filter(
      (component) => component.key !== childData.key,
      userData.components
    );
    userData.components.push({
      ...pick(["key", "text"], childData),
      value: userInput,
    });
    const nextValue = showValidationMsg + 1;
    setShowValidationMsg(nextValue);
    setUserData(userData);
  };

  const showChildValidationMsg = (child) => {
    const childComponents = userData.components || [];
    const component = find(
      (component) => component.key === child.key,
      childComponents
    );
    return (
      showValidationMsg &&
      (!component || (component && isEmpty(component.value)))
    );
  };

  const showParentValidationMsg = () => {
    return showValidationMsg && userData.value && !userData.value.value;
  };
  return (
    <div className="details-container">
      <h2 className="title">{component.text}</h2>
      <RadioGroup
        options={component.options}
        onChange={(data) => handleParentComponent(data, component)}
        value={userData.value}
      />
      <Message message={showParentValidationMsg() ? validationMsg : ""} />
      <div className="group-container">
        {checkForUserInputYes() &&
          component.components.map((child) => (
            <div className="question-container" key={child.key}>
              <Label text={child.text} />
              <RadioGroup
                options={child.options}
                onChange={(data) => handleChildComponent(data, child)}
                value={getSavedRadioOptionValue(child.key, userData)}
              />
              <Message
                message={showChildValidationMsg(child) ? validationMsg : ""}
              />
            </div>
          ))}
      </div>
      <div className="button-container">
        <Button
          className="next-button"
          text="Prev"
          onClick={handlePrevButtonClick}
        />
        <Button
          className="next-button"
          text="Next"
          onClick={handleNextButtonClick}
        />
      </div>
    </div>
  );
};

export {Survey};
