import React from "react";
import joinClasses from "join-classes";

const Input = (props) => {
  const {
    className,
    disabled = false,
    onChange,
    placeholder,
    type = "text",
    value = "",
    field,
    error,
    ...others
  } = props;

  const styles = {};
  if (error) {
    styles.borderColor = 'red';
  }

  const onValueChange = (event) => {
    props.onChange({ [props.field]: event.target.value });
  };

  return (
    <input
      style={styles}
      className={joinClasses("form-control", className)}
      type={type}
      value={value}
      onChange={onValueChange}
      disabled={disabled}
      placeholder={placeholder}
      {...others}
    />
  );
};

export default Input;
