import {Redirect} from "react-router-dom";
import generateReport from "../../util/report-generator";
import {getDetails} from "../../util/storage";
import "./report.css";

const Report = () => {
  const userDetails = getDetails("userDetails");
  const componentData = getDetails("componentData");
  if (
    !userDetails ||
    !componentData ||
    (componentData && !componentData.length)
  ) {
    return <Redirect to="/user-details" />;
  }
  const findings = generateReport(componentData, userDetails);

  return (
    <div className="report-container">
      <h2 className="report-heading">CHILD DEVELOPMENTAL REPORT</h2>
      <div className="report-content">
        <h4>CHILD DETAILS</h4>
        <div className="child-details">
          <div className="child-details-group">
            <span className="child-label">Child Name</span>:{" "}
            <span className="child-value">{userDetails.childName}</span>
          </div>
          <div className="child-details-group">
            <span className="child-label">Email</span>:{" "}
            <span className="child-value">{userDetails.email}</span>
          </div>
          <div className="child-details-group">
            <span className="child-label">Parent Name</span>:{" "}
            <span className="child-value">{userDetails.parentName}</span>
          </div>
          <div className="child-details-group">
            <span className="child-label">Phone Number</span>:{" "}
            <span className="child-value">{userDetails.phoneNumber}</span>
          </div>
          <div className="child-details-group">
            <span className="child-label">Screen Period</span>:{" "}
            <span className="child-value">
              {userDetails.screenPeriodValue.text}
            </span>
          </div>
        </div>
        <h4>OVER ALL FINDINGS</h4>
        <ul className="child-findings">
          {findings.map((text, index) => (
            <li key={index}>{text}</li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export {Report};
