import { forEach, map, reduce, uniq } from 'rambda';
import { getKeyData } from "./util";

const checkEmptyValue = (component) => {
    return component && component.value && component.value.value;
};

const getPointsMap = (componentData) => {
    const points = {
        total: 0
    };
    forEach((component) => {
        const reducer = (prev, current) => (prev + (checkEmptyValue(current) ? current.value.value : 0));
        points[component.key] = reduce(reducer, 0, component.components);
        points.total += points[component.key];
    }, componentData);
    return points;
};

const getSumPointsOfComponents = (componentIds, pointsMap) => {
    const reducer = (prev, componentId) => (prev + (pointsMap[componentId] ? pointsMap[componentId] : 0));
    return reduce(reducer, 0, componentIds);
};

const statisfiesConditionsAganistPoints = (rule, pointsMap) => {
    const conditonResult = map((condition) => {
        const points = getSumPointsOfComponents(condition.target, pointsMap);
        return (condition.upperRange >= points && condition.lowerRange <= points);
    }, rule.conditions);

    return !conditonResult.includes(false);
};

const checkConditions = (pointsMap, rules) => {
    let resultsTexts = [];
    forEach((rule) => {
        if (statisfiesConditionsAganistPoints(rule, pointsMap)) {
            resultsTexts = [
                ...resultsTexts,
                ...rule.texts
            ]
        }
    }, rules);
    return uniq(resultsTexts);
};

const generateReport = (componentData, userDetails) => {
    const pointsMap = getPointsMap(componentData);
    const config = getKeyData(userDetails.screenPeriodValue.key)
    const rules = config.rules;
    const reportTexts = checkConditions(pointsMap, rules);
    if (!reportTexts.length) {
        return ["Your child is healthy. Take care."]
    }
    return reportTexts;
};


export default generateReport;