import React, { useState } from "react";
import "./user-details.css";
import Label from "../../components/label/label";
import Input from "../../components/input/input";
import Message from "../../components/message/message";
import Select from "../../components/select/select";
import Button from "../../components/button/button";
import { validate } from "../../util/validate";
import { useHistory } from "react-router-dom";
import { storeDetails, getDetails } from "../../util/storage";
import config from "../../config/config";
import { forEach } from "rambda";

const UserDetails = (props) => {
  const history = useHistory();
  const defaultState = {
    parentName: "",
    childName: "",
    phoneNumber: "",
    email: "",
    screenPeriodValue: "",
  };
  const savedData = getDetails("userDetails");

  const [state, setState] = useState(savedData ? savedData : defaultState);

  const [errors, setErrors] = useState({
    parentName: "",
    childName: "",
    phoneNumber: "",
    email: "",
    screenPeriodValue: "",
  });

  const onChange = (value) => {
    setState({
      ...state,
      ...value,
    });
  };


  const checkForAnyError = (validationErrors) => {
    let invalid = false;
    forEach(field => {
      invalid = validationErrors[field] || invalid;
    }, Object.keys(validationErrors));
    return invalid;
  }

  const handleButtonClick = () => {
    const validationErrors = validate(state);
    setErrors({ ...errors, ...validationErrors });

    if (!checkForAnyError(validationErrors)) {
      storeDetails("userDetails", state);
      const parentCompoentId = state.screenPeriodValue.key;
      const childComponentId = state.screenPeriodValue.components[0].key
      history.push(`/survey/${parentCompoentId}/inner/${childComponentId}`);
    }
  };

  return (
    <div className="details-container">
      <Label text="Parent Name" />
      <Input
        placeholder="Parent Name"
        field="parentName"
        onChange={onChange}
        error={!!errors.parentName}
        value={state.parentName}
      />
      <Message message={errors.parentName} />
      <Label text="Parent Email" />
      <Input
        placeholder="Parent Email"
        field="email"
        onChange={onChange}
        error={!!errors.email}
        value={state.email}
      />
      <Message message={errors.email} />
      <Label text="Phone Number" />
      <Input
        placeholder="Phone Number"
        type="number"
        field="phoneNumber"
        onChange={onChange}
        error={!!errors.phoneNumber}
        value={state.phoneNumber}
      />
      <Message message={errors.phoneNumber} />
      <Label text="Child Name" />
      <Input
        placeholder="Child Name"
        field="childName"
        onChange={onChange}
        error={!!errors.childName}
        value={state.childName}
      />
      <Message message={errors.childName} />
      <Label text="Screen Period" />
      <Select
        field="screenPeriodValue"
        onChange={onChange}
        error={!!errors.screenPeriodValue}
        value={state.screenPeriodValue}
        options={config}
      />
      <Message message={errors.screenPeriodValue} />
      <Button className="next-button" text="Next" onClick={handleButtonClick} />
    </div>
  );
};

export { UserDetails };
