import React from "react";
import {
    Redirect
} from "react-router-dom";

import { getDetails } from "./storage";

const userDetailsExists = () => {
    const userDetails = getDetails('userDetails');
    return userDetails ? true : false;
};

const DataRoute = ({ component: Component, ...props }) => {
    if (!userDetailsExists()) {
        return (<Redirect to={`/user-details`} />);
    }
    return (<Component {...props} />);
};

export default DataRoute;