import React from "react";
import joinClasses from "join-classes";
import "./button.css";

const Button = (props) => {
  const { variant, text, href, disabled, onClick, ...others } = props;
  return (
    <button
      className={joinClasses("button", variant)}
      disabled={disabled}
      onClick={onClick}
      {...others}
    >
      {text}
    </button>
  );
};

export default Button;
